import { CardWraper,Button,P,Top,Botom,Span,Mid} from "./Card.style"

function Card({price,type}) {
    return (
        <CardWraper>
            <Top>
                <P title>$<Span>{price}</Span>/month</P>
                <Button>{type} Plan</Button>
            </Top>
            <Mid>
                <P>Lorem ipsum dolor sit amet.</P>
                <P>50+ Prebuilt Website</P>
                <P>Exclusive Support</P>
                <P>Premiam Plagin</P>
            </Mid>
            <Botom>
              <Button bottom>Join Now</Button>
            </Botom>  
        </CardWraper>
    )
}

export default Card
