import styled from "styled-components";

export  const Container = styled.div`
  width: 100%;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
`
export const Shape = styled.div`
    clip-path: polygon(100% 0%, 65% 0%, 43% 100%, 100% 100%);
    background-color: crimson;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
`