import { Box,Circle,Pluse,Circle2 } from "./Animations.style"

function Animations() {
    return (
        <>
           <Box/>
           <Circle/>
           <Circle2/>
           <Pluse>+</Pluse>
           <Pluse two >+</Pluse>
           <Pluse three >+</Pluse>
           <Pluse four >+</Pluse>
           <Pluse five >+</Pluse>

        </>
    )
}

export default Animations
