import styled from "styled-components";
import {FaBars,FaTimes} from 'react-icons/fa';


export const Container = styled.div`
    width: 100%;
    height: 60px;
    position:sticky;
    right: 0;
    top: 0;
    z-index: 1000;
    background-color: #fff;
    box-shadow: ${({scroll})=>(scroll && `5px 5px 15px rgba(0,0,0,0.52)`)};

    @media screen and (max-width:400px){
      height: 50px;
    }
`
export const Wraper = styled.div`
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
`
export const Left = styled.div`
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  
`
export const Right = styled.div`
    width: 40%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
`
export const Logo = styled.h1`
    font-weight: bold;
    text-decoration: underline crimson;
    cursor: pointer;

    @media screen and (max-width:980px){
      margin-right: 30px;
    }
    @media screen and (max-width:400px){
      font-size: 20px;
    }
`
export const Menu = styled.ul`
    display: flex;
    transition:0.5s ease;

    @media screen and (max-width:720px){
      position: absolute;
      width: 100%;
      height: 100vh;
      left: ${({open})=>(open ? 0 : "-1000px")};
      top: 0;
      background-color: rgb(28, 96, 255);
      z-index: 1000;
      flex-direction: column;

    }

  
`
export const Menuitem = styled.li`
    list-style: none;
    margin-right: 20px;
    cursor: pointer;
    transition: .5s ease;
    font-size: 20px;
    color: gray;
    &:hover{
      color: crimson;
    }

    @media screen and (max-width:720px){
        width: 100%;
        height: 60px;
        color: #fff;
        padding: 10px;
        text-align: center;
        margin-top: 30px;
        &:nth-child(1){
            margin-top: 80px;
        }
    }

    @media screen and (max-width:400px){
      margin-top: 20px;

      &:nth-child(1){
            margin-top: 40px;
        }
    }
`
export const Button = styled.button`
    border: 2px solid white;
    padding: 10px 15px;
    background-color:crimson;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
    cursor: pointer;
    border-radius: 20px;
    font-size: 16px;
    transition: .5s ease;
    &:hover{
        background-color: white;
        color: red;
    }
    @media screen and (max-width:720px){
      display: none;
    }
`
export const Menubtn = styled(FaBars)`
    display: none;
    font-size: 24px;
    color: #000;
    cursor: pointer;
    z-index: 10000;

    @media screen and (max-width:720px){
      display: block;
    }
`
export const Closebtn = styled(FaTimes)`
    display: none;
    font-size: 24px;
    color: #fff;
    cursor: pointer;
    z-index: 10000;

    @media screen and (max-width:720px){
      display: block;
    }
`