import { Container ,Wraper,Shapes } from "./Pricing.style"
import Card from "./Card"

function Pricing() {
    return (
        <Container id="pricing">
            <Shapes/>
            <Wraper>
             <Card price="10" type="Basic"/>
             <Card price="20" type="Premium"/>
             <Card price="30" type="Advance"/>
            </Wraper>
        </Container>
    )
}

export default Pricing
