import { useState } from "react"
import { Container,Wraper,Left,Right,Logo,Menu,Menuitem,Button,Menubtn,Closebtn} from "./Navbar.style"
import { Link } from "react-scroll"

function Navbar() {

    const [open,setOpen] = useState(false)
    const [scroll,setScroll] = useState(false)


    const Menuopen = ()=>{
        setOpen(!open)
    }

    window.addEventListener("scroll",()=>{
        if(window.scrollY>60){
           setScroll(true)
        }
        else{
            setScroll(false)
        }
    })

    return (
        <Container scroll={scroll}>
            <Wraper>
                <Left>
                    <Link to="home" smooth={true} duration={1000}><Logo>Saykot</Logo></Link>
                 <Menu open={open}>
                  <Link to="home" smooth={true} duration={1000}> <Menuitem onClick={Menuopen}>Home</Menuitem> </Link> 
                  <Link to="features" smooth={true} duration={1000}> <Menuitem onClick={Menuopen}>Features</Menuitem> </Link> 
                  <Link to="service" smooth={true} duration={1000}> <Menuitem onClick={Menuopen}>Services</Menuitem> </Link> 
                  <Link to="pricing" smooth={true} duration={1000}> <Menuitem onClick={Menuopen}>Pricing</Menuitem> </Link> 
                  <Link to="contact" smooth={true} duration={1000}> <Menuitem onClick={Menuopen}>Contact</Menuitem> </Link> 
                 </Menu>
                </Left>
                <Right>
                  <Button>Join Now</Button>
                 {!open ? <Menubtn onClick={Menuopen}/>:<Closebtn onClick={Menuopen}/>}
                </Right>  
            </Wraper>
        </Container>
    )
}

export default Navbar
