import styled from "styled-components";

export const Box = styled.div`
    position: absolute;
    top: 50%;
    left:0 ;
    width: 60px;
    height: 60px;
    border: 3px solid rgba(255,0,0,.5) ;
    background-color: transparent;
    animation: round 5s linear infinite;

    @keyframes round {
        0%{
            transform:translateX(-50%) rotate(0deg);
        }
      100%{
        transform:translateX(-50%) rotate(360deg);
      }
    }

    @media screen and (max-width:400px){
        top: 30%;  
        width: 40px;
        height: 40px; 
    }
`;

export const Circle = styled.div`
    position: absolute;
    top: 120px;
    left:calc(100% - 30px) ;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color:  rgba(255,255,255,.5);
    animation: zoom 5s linear infinite;

    @keyframes zoom{
        0%{
            transform: scale(1);
        }
        50%{
            transform: scale(1.5);
        }
        100%{
            transform: scale(1);
        }
    }
`;
export const Circle2 = styled.div`
    position: absolute;
    top: 80px;
    left:40% ;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color:  rgba(255,0,0,.3);
    animation: zoom 5s linear infinite;

    @keyframes zoom{
        0%{
            transform: scale(1);
        }
        50%{
            transform: scale(1.5);
        }
        100%{
            transform: scale(1);
        }
    }
`;

export const Pluse = styled.p`
    position: absolute;
    top: ${({two,three,four,five})=>(two? "60px" : "120px" && three ? "180px" : "120px" && five ? "260px" : "120px")} ;
    left:${({two,three,four,five})=>(two ? "160px":"60px" && three ? "150px" : "60px"  && four ? "70%" : "60px" && five ? "60%" : "60px")} ;
    font-size: ${({two,three})=>(two ? "25px" : "20px" && three ? "28px" : "20px")};
    font-weight: bold;
    color:  ${({four,five})=>( five ? "#fff" : "red" && four ? "#fff" : "red")};
    animation: zoom ${({two,three})=>(two ? "2s" : "1s" && three ? "3s" : "1s")} linear infinite;

    @keyframes zoom{
        0%{
            transform: scale(1);
        }
        50%{
            transform: scale(1.5);
        }
        100%{
            transform: scale(1);
        }
    }

    @media screen and (max-width:720px){
        display: none;
    }
`;