import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    position: relative;

`
export const Left = styled.div`
    width: 50%;
    height: 100%;

    @media screen and (max-width:900px){
    display: none;
    }
`
export const Right = styled.div`
    width: 50%;
    height: 100%;
    padding: 80px 40px;

    @media screen and (max-width:1072px){
        padding: 40px 20px;
    }
    @media screen and (max-width:980px){
        padding:20px;
    }
    @media screen and (max-width:900px){
       width: 100%;
    }
`
export const Images = styled.img`
    width: 100%;
    height: 100%;
`
export const TextBox = styled.div`
   width: 100%;
`
export const Title = styled.h1`
  font-size: 40px;
  margin:20px 0 30px 0 ;
  font-weight: 600;
  color: #fff;

  @media screen and (max-width:980px){
    font-size: 30px;
    margin:20px 0 ;
    }
    @media screen and (max-width:900px){
        text-align: center;
        margin-top: 40px;
    }
 
    @media screen and (max-width:460px){
        text-align: center;
        margin-top: 20px;
    }
    @media screen and (max-width:400px){
        font-size: 24px;
        margin-top: 15px;
    }
`

export const Desc = styled.p`
   font-size: 20px;
   margin-top: 30px;
   color: rgba(255,255,255,.7);
   @media screen and (max-width:400px){
    font-size: 16px;
   margin-top: 10px;
    }
`
export const Playimg = styled.img`
    width: 20px;
    height: 20px;
    margin-right: 5px;
    border-radius: 50%;
    background-color: rgb(229,47,108);
`
export const Button = styled.button`
    padding: 10px 15px;
    height: 40px;
    background-color:white;
    border-radius: 20px;
    color: rgb(229,47,108);
    border: none;
    font-size: 16px;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 30px;
    display: flex;
    align-items: center;
    transition: .5s;

   &:hover{
       background-color: rgb(229,47,108);
       color: white;
   }

   @media screen and (max-width:600px){
     margin-top: 15px;
    }
   @media screen and (max-width:480px){
     margin-top: 10px;
    }
  `;
  
export const Shape = styled.div`
    clip-path: polygon(100% 0%, 43% 0%, 43% 100%, 100% 100%);
    background-color:rgb(229,47,108);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;

    @media screen and (max-width:900px){
        clip-path: polygon(100% 0%, 0% 0%, 0% 100%, 100% 100%);
    }
`
export const VideoBox = styled.div`
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    display: ${({open})=>(open ? "bolck" : "none")};
`
export const VideoWraper = styled.div`
    height: 60%;
    width: 80%;

    @media screen and (max-width: 768px){
     height: 60%;
     width: 100%;
    }
    @media screen and (max-width: 680px){
     height: 50%;
     width: 100%;
    }
    @media screen and (max-width: 400px){
     height: 40%;
     width: 100%;
    }
`
export const Iframe = styled.iframe`
    width: 100%;
    height: 100%;
`
export const Close = styled.span`
    background-color: white;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    color: red;
    position: absolute;
    top: 100px;
    right: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 900;
    cursor: pointer;
    font-size: 20px;

    @media screen and (max-width: 768px){
        right: 0;
    }

    @media screen and (max-width: 680px){
        top: 20%;
        right: 0;
        height: 30px;
        width: 30px;
        font-size: 18px;
    }
    @media screen and (max-width: 400px){
        top: 26% ;
        right: 0;
        height: 30px;
        width: 30px;
        font-size: 18px;
    }
`