import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    min-height: 100vh;
    background: #fff;
    overflow: hidden;
`
export const Wraper = styled.div`
    width: 100%;
    height: 50%;
    display: flex;
    flex-wrap: wrap;
    @media screen and (max-width:1020px){
        justify-content: space-between;
        align-items: center;
    }
    @media screen and (max-width:600px){
       padding: 20px;
    }
`
export const Info = styled.div`
    width: 45%;
    height: 100%;
    padding: 80px 100px 0 120px;

    @media screen and (max-width:1020px){
        padding: 60px 60px 0 80px;
    }
    @media screen and (max-width:740px){
        padding: 30px 30px 0 60px;
    }
    @media screen and (max-width:620px){
        padding:20px 10px 0 20px;
    }
    @media screen and (max-width:400px){
        width: 100%;
        padding:40px;
    }
`
export const ContactBox = styled.div`
    width: 350px;
    height: 380px;
    padding: 40px 30px;
    margin-top: 60px;
    margin-left: 30px;
    background: #fff;
    z-index: 2;
    -webkit-box-shadow: -3px 4px 32px -11px rgba(0,0,0,0.2); 
    box-shadow: -3px 4px 32px -11px rgba(0,0,0,0.2);

    @media screen and (max-width:1020px){
       width: 45%;
       height: 80%;
       margin-right: 30px;
    }
    @media screen and (max-width:620px){
      padding: 20px;
      margin:0 ;
    }
  
    @media screen and (max-width:400px){
        width: 100%;
        margin: 0;
        padding: 40px;
    }

`
export const Footer = styled.div`
    width: 100%;
    height: 50%;
    background: rgb(255,59,73);
    color: #fff;
    padding: 100px 100px 60px;
    display: flex;
    flex-direction: column;

    @media screen and (max-width:1020px){
        padding: 60px 60px 40px 60px;
    }
    @media screen and (max-width:720px){
        padding: 40px;
    }
   
`
export const Title = styled.h1`
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: ${({box})=>(box && "10px")};
`
export const Desc = styled.p`
    margin-top: 20px;
    @media screen and (max-width:720px){
        font-size: 16px;
    }
`   
export const Message = styled.p`
    margin-top: 40px;
`   
export const Phone = styled.p`
    margin-top: 20px;
`   
export const Address = styled.p`
    margin-top: 20px;
`   
export const Button = styled.button`
    margin-top: 10px;
    background: rgb(255,59,73);
    color: white;
    width: 100%;
    padding: 5px;
    border: none;
    cursor: pointer;
    
`   
export const Input = styled.input`
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 2px solid rgb(255,59,73);
    &:focus{
        outline: none;
    }
`
export const Textarea = styled.textarea`
    width: 100%;
    border: 2px solid rgb(255,59,73) ;
    padding: 20px;
    margin:10px 0 ;

    &:focus{
        outline: none;
    }
`

export const Top = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width:600px){
        flex-wrap: wrap;
    }
`
export const Bottom = styled.div`
    width: 100%;
   margin-top: 30px;
`
export const Left = styled.div`
    width: 30%;

    @media screen and (max-width:800px){
       width: 38%;
    }
    @media screen and (max-width:600px){
       width: 100%;
       margin-bottom: 20px;
    }
`
export const Mid = styled.div`
    width: 30%;
    margin-top: 30px;

    @media screen and (max-width:800px){
       width: 38%;
       margin-top:0;
    }
    @media screen and (max-width:600px){
       width: 100%;
       margin-bottom: 20px;
    }
`
export const Right = styled.div`
    width: 30%;
    display: flex;
    align-items: center;
    @media screen and (max-width:800px){
        width: 20%;
    }
    @media screen and (max-width:600px){
       width: 100%;
    }

`
export const List = styled.ul`
    list-style: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
    flex-wrap: wrap;
`
export const Listitem = styled.li`
    display: inline-block;
    letter-spacing: 1px;
    font-size: 14px;
    cursor: pointer;
    padding: 10px;
    transition:.5s ease;

    &:hover{
        color: green;
    }

    @media screen and (max-width:600px){
        font-size: 12px;
    }
`
export const Logo = styled.h1`
    cursor: pointer;
    font-size: 25px;
    text-transform: uppercase;

    @media screen and (max-width:720px){
        font-size: 20px;
    }
`
