import { Container,Left,Right,Shape,Button,Images,TextBox,Title,Desc,Playimg,VideoBox,VideoWraper,Iframe,Close} from "./Service.style"
import Card from "./Card"
import Photo from "../../img/young-woman.jpg"
import Play from "../../img/play.png"
import { useState } from "react"


function Service() {

    const [open,setOpen] = useState(false)

    return (
        <Container id="service">
            <Left>
              <Images src={Photo}/>
            </Left>
            <Right>
               <TextBox>
                   <Title>Simple Process To Start </Title>
                   <Desc>Lorem ipsum dolor sit amet consectetur adipisicing elit. Delectus suscipit perspiciatis magnam saepe ad quod accusantium consequatur, cupiditate illum debitis impedit eaque quisquam consectetur voluptatem soluta in, consequuntur exercitationem similique?</Desc>
                   <Card/>
                   <Button onClick={()=>{setOpen(!open)}}> <Playimg src={Play}/> How it works</Button>
               </TextBox>
            </Right>
            <Shape/>
            <VideoBox open={open}>
                <Close onClick={()=>{setOpen(!open)}}>X</Close>
               <VideoWraper>
                 {open &&  <Iframe src="https://www.youtube.com/embed/9_s_Essow6s"title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen="true"></Iframe> }  
               </VideoWraper>
            </VideoBox>
        </Container>
    )
}

export default Service
