import styled from "styled-components";

export const Container = styled.div`
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
    position: relative;

    @media screen and (max-width:400px){
        padding-bottom:20px ;
    }
`
export const Left = styled.div`
    width: 50%;
    padding: 40px;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width:720px){
        width: 100%;
        /* height: 30%; */
        padding-top: 20px;
        padding-bottom: 0;
    }
    /* @media screen and (max-width:400px){
        align-items: flex-start;
        height: 25%;
       padding-top: 20px;

    } */
`
export const Right = styled.div`
    width: 50%;
    display: flex;
    align-items: center;

    @media screen and (max-width:720px){
        width: 100%;
        /* height: 70%; */
        align-items: flex-start;
    }
`
export const Images = styled.img`
    width: 100%;

    @media screen and (max-width:720px){
        width: 300px;
    }
    @media screen and (max-width:470px){
        width: 250px;
    }
    @media screen and (max-width:400px){
        width: 150px;
    }
`
export const TextBox = styled.div`
   width: 100%;
  padding: 0 40px;


  @media screen and (max-width:812px){
    padding: 0 20px;
    }
`
export const Title = styled.p`
  font-size: 50px;

  @media screen and (max-width:720px){
       font-size: 30px;
       text-align: center;
    }
  @media screen and (max-width:400px){
       font-size: 20px;
       text-align: center;
    }
  @media screen and (max-width:812px){
       font-size: 40px;
    }
`
export const Subtitle = styled.p`
   font-size: 20px;
   margin-top: 30px;

   @media screen and (max-width:720px){
    font-size: 18px;
    margin-top: 20px;
    }
`
export const Desc = styled.p`
   font-size: 18px;
   margin-top: 20px;
   color: gray;

   @media screen and (max-width:720px){
    font-size: 16px;
    margin-top: 18px;
    color: #000;
    }

`
export const Button = styled.button`
    padding: 10px 15px;
    height: 40px;
    background-color: rgb(207,115,220);
    border-radius: 20px;
    color: white;
    border: none;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    margin-top: 20px;
    transition: 0.5s ease;

    &:hover{
        background-color: white;
        color:rgb(207,115,220) ;
    }

    @media screen and (max-width:720px){
        background-color: #fff;
        color:  rgb(207,115,220);

        &:hover{
        background-color:rgb(207,115,220);
        color: #fff ;
    }
    }
`
export const Shape = styled.div`
    clip-path: polygon(43.1% 0%, 0% 0%, 0% 100%, 43.1% 100%);
    /* background-color: #FF3369; */
    background-color: rgb(207,115,211);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
`

