import styled from "styled-components";

export const CardWraper = styled.div`
background-color: white;
    width: 30%;
    padding: 40px;
    border-radius: 5px;
    -webkit-box-shadow: 5px 5px 15px 5px rgba(0,0,0,0.44); 
    box-shadow: 0px 0px 10px rgba(0,0,0,0.3);

    @media screen and (max-width:1200px){
      padding: 30px;
    }
    @media screen and (max-width:1000px){
      padding: 25px;
    }
    @media screen and (max-width:978px){
      padding: 20px;
    }
    @media screen and (max-width:840px){
      padding: 15px;
    }
    @media screen and (max-width:720px){
      width: 47%;
      margin-top: 10px;
      padding: 10px;
      padding-top: 0;
    }
    @media screen and (max-width:400px){
      width: 100%;
      margin-top: 20px;
    }
`
export const P = styled.p`
    font-size: 20px;
    font-weight: 500;
    margin:20px 0;
    display: ${({title})=>(title && "flex")};
    align-items: center;
    justify-content: center;

    @media screen and (max-width:840px){
      font-size: 16px;
      margin:15px 0 ;
    }
    @media screen and (max-width:720px){
        margin-top: 10px 0;
    }
`
export const Span = styled.span`
    font-size: 45px;
    font-weight: 600;

    @media screen and (max-width:840px){
      font-size: 35px;
    }

    @media screen and (max-width:720px){
      font-size: 30px;
    }
`

export const Button = styled.button`
    padding: 10px 15px;
    border-radius: 20px;
    border:2px solid crimson;
    font-weight: 600;
    background-color: ${({bottom})=>(bottom ? "crimson" : "#fff")};
    color: ${({bottom})=>(bottom ? "#fff ": "crimson")};
    cursor: pointer;
    margin-top: ${({bottom})=>(bottom && "20px")};
    transition: .5s ease;

    &:hover{
        background-color: ${({bottom})=>(!bottom ? "crimson" : "#fff")};
        color: ${({bottom})=>(!bottom ? "#fff ": "crimson")};
    }
    @media screen and (max-width:720px){
      font-size: 16px;
      padding: 8px 10px;
      margin-top: ${({bottom})=>(bottom && "10px")};
    }
    
`
export const Top = styled.div`
    width: 100%;
    text-align: center;
`   
export const Botom = styled.div`
    width: 100%;
    text-align: center;
`   
export const Mid = styled.div`
    padding: 10px 0 0 20px;
    margin-bottom: 10px;
`